/*=====================
  Базовая типография
=====================*/

@import url('https://fonts.googleapis.com/css?family=Lora');
@import url('https://fonts.googleapis.com/css?family=Alegreya+SC:400,700&subset=cyrillic');

@font-face {
	font-family: 'Circe';
	src: url('/fonts/Circe-Thin.eot');
	src: url('/fonts/Circe-Thin.eot?#iefix') format('embedded-opentype'),
		url('/fonts/Circe-Thin.woff2') format('woff2'),
		url('/fonts/Circe-Thin.woff') format('woff'),
		url('/fonts/Circe-Thin.ttf') format('truetype'),
		url('/fonts/Circe-Thin.svg#Circe-Thin') format('svg');
	font-weight: 100;
	font-style: normal;
}

